import tmdb from "./img/tmdblogo.jpg";
import './App.css';
import { useState } from "react";

function App() {
  const [ click, setClick ] = useState(false);
  const closeMobileMenu = () => setClick(false);
  const handleClick = () => setClick(!click);




  return (
    <div>
      <nav className="navbar">
        <div className="navbar-leftside">
          <img src={tmdb} alt="TMDB LOGO" />
        </div>

        <div 
          className={click ? "menu-btn open" : "menu-btn"}
          onClick={handleClick}
          >
          <div className="menu-btn_burger"></div>
        </div>
        
        <div className="navbar-rightside">
          <ul className={click ? 'nav-menu active' : 'nav-menu'}>
            
            <li className="nav-item">
              <a 
              className="nav-links" onClick={closeMobileMenu}>MOVIES</a>
            </li>
            
            <li className="nav-item">
              <a className="nav-links" onClick={closeMobileMenu}>TVSHOES</a>
            </li>
            
            <li className="nav-item">
              <a className="nav-links" onClick={closeMobileMenu}>ACTORS</a>
            </li>

          </ul>
        </div>

      </nav>
  </div>
    
  );
}

export default App;
